import React from "react"
import { Box, IconButton, Typography } from "@rp/react/web/mui/core"
import { DeleteOutline, DragHandle } from "@rp/react/web/mui/icons"
import { FinanceDataTabs } from "../../../hooks/useWatchlistsFinanceDataTabNavigation"
import { watchlistDataTables } from "../../tables"
import { SelectedColumnWithId } from "../types"

type SelectedColumnItemProps = {
  item: SelectedColumnWithId
  onDelete?: (column: SelectedColumnWithId) => void
  dragOptions?: unknown
  componentProps?: unknown
  variant?: "normal" | "preview"
}

// Flexible component for rendering selected columns in both normal and preview states
const SelectedColumnItem = React.forwardRef<HTMLDivElement, SelectedColumnItemProps>(
  (props, ref) => {
    const {
      item: column,
      onDelete,
      dragOptions: _dragOptions,
      componentProps: _componentProps,
      variant = "normal",
      ...restProps
    } = props

    const isPreview = variant === "preview"

    return (
      <Box
        ref={ref}
        {...restProps}
        sx={{
          p: 1,
          my: 1,
          backgroundColor: isPreview ? "primary.main" : "elevation.dp08",
          borderRadius: 1,
          border: 1,
          borderColor: isPreview ? "primary.light" : "divider",
          position: "relative",
          display: "flex",
          alignItems: "center",
          gap: 1,
          cursor: isPreview ? "default" : "grab",
          ...(isPreview && {
            opacity: 0.8,
            transform: "rotate(2deg)",
          }),
          ...(!isPreview && {
            "&:hover": {
              backgroundColor: "elevation.dp24",
              borderColor: "primary.main",
              "& .delete-button": {
                opacity: 1,
              },
            },
            "&:active": {
              cursor: "grabbing",
            },
          }),
        }}
      >
        <DragHandle
          sx={{
            fontSize: 16,
            color: isPreview ? "text.primary" : "text.secondary",
          }}
        />

        <Box sx={{ flex: 1 }}>
          <Typography
            sx={{
              color: "text.primary",
              fontSize: 16,
              fontWeight: 400,
            }}
          >
            {column.displayName}
          </Typography>
          <Typography
            sx={{
              color: "text.secondary",
              fontSize: 12,
              fontWeight: 400,
            }}
          >
            {watchlistDataTables[column.sourceTab as FinanceDataTabs]?.tableLabel}
          </Typography>
        </Box>

        <IconButton
          onClick={e => {
            e.stopPropagation()
            onDelete?.(column)
          }}
        >
          <DeleteOutline sx={{ fontSize: 16 }} />
        </IconButton>
      </Box>
    )
  },
)

SelectedColumnItem.displayName = "SelectedColumnItem"

export { SelectedColumnItem }
